import React, { useState } from 'react';
import { Container, Typography, Button, Dialog, DialogActions, DialogContent, DialogTitle, FormControlLabel, Checkbox, Select, MenuItem, TextField, InputLabel, FormControl } from '@mui/material';
import { SelectChangeEvent } from '@mui/material';

interface Service {
  name: string;
  price: number;
}

const App = () => {
  const [selectedPlan, setSelectedPlan] = useState<{ name: string; price: number }>({ name: 'Perusjsenyys', price: 30 });
  
  const [selectedServices, setSelectedServices] = useState<Record<string, boolean>>({
    'Personal Trainer': false,
    'Hieronta': false,
    'Inbody-mittaus': false,
  });

  const [selectedLocation, setSelectedLocation] = useState('');
  
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [phone, setPhone] = useState('');
  const [age, setAge] = useState('');
  const [isDialogOpen, setIsDialogOpen] = useState(false);
  const [error, setError] = useState('');

  const membershipPlans = [
    { name: 'Perusjsenyys: yksi sali kytss klo 9 - 15', price: 30 },
    { name: 'Premium-jsenyys: yksi sali 24h', price: 50 },
    { name: 'VIP-jsenyys: kaikki salit 24h', price: 80 },
  ];

  const locations = ['Helsinki', 'Espoo', 'Vantaa', 'Tampere', 'Turku'];

  const services: Service[] = [
    { name: 'Personal Trainer', price: 40 },
    { name: 'Hieronta', price: 50 },
    { name: 'Inbody-mittaus', price: 30 },
  ];

  const handleServiceChange = (service: string) => {
    setSelectedServices(prev => ({
      ...prev,
      [service]: !prev[service],
    }));
  };

  const totalPrice = selectedPlan.price + Object.keys(selectedServices).reduce((sum, service) => {
    return selectedServices[service] ? sum + services.find(s => s.name === service)!.price : sum;
  }, 0);

  const handlePlanChange = (event: SelectChangeEvent<string>) => {
    const selectedPlan = membershipPlans.find(plan => plan.name === event.target.value);
    if (selectedPlan) setSelectedPlan(selectedPlan);
  };

  const handleDialogClose = () => {
    setIsDialogOpen(false);
  };

  const handleDialogOpen = () => {
    if (!name || !email || !phone || !age || !selectedLocation) {
      setError('Tyt kaikki kentt ennen kuin jatkat.');
      return;
    }
    setError('');
    setIsDialogOpen(true);
  };

return (
    <Container maxWidth="sm" sx={{ mt: 4 }}>
      <Typography variant="h4" gutterBottom>
        Kuntosalin jsenyys
      </Typography>

      <Typography variant="h6">Valitse jsenyyspaketti</Typography>
      <Select fullWidth value={selectedPlan.name} onChange={handlePlanChange}>
        {membershipPlans.map(plan => (
          <MenuItem key={plan.name} value={plan.name}>
            {plan.name} - {plan.price}/kk
          </MenuItem>
        ))}
      </Select>

      <Typography variant="h6" sx={{ mt: 2 }}>
        Valitse paikkakunta
      </Typography>
      <Select
        fullWidth
          value={selectedLocation}
          onChange={(e) => setSelectedLocation(e.target.value)}
          displayEmpty
        >
      <MenuItem value="" disabled>
        Valitse paikkakunta
      </MenuItem>
        {locations.map((location) => (
        <MenuItem key={location} value={location}>
        {location}
        </MenuItem>
        ))}
        </Select>

      <Typography variant="h6" sx={{ mt: 2 }}>
        Valitse lispalvelut ensimmiselle kerralle
      </Typography>
      {services.map(service => (
        <FormControlLabel
          key={service.name}
          control={
            <Checkbox
              checked={selectedServices[service.name]}
              onChange={() => handleServiceChange(service.name)}
            />
          }
          label={`${service.name} (${service.price})`}
        />
      ))}

      <Typography variant="h6" sx={{ mt: 2 }}>
        Nimi
      </Typography>
      <TextField
        fullWidth
        value={name}
        onChange={(e) => setName(e.target.value)}
        placeholder="Nimi"
        type="Name"
        sx={{ mb: 2 }}
      />

      <Typography variant="h6" sx={{ mt: 2 }}>
        Shkposti
      </Typography>
      <TextField
        fullWidth
        value={email}
        onChange={(e) => setEmail(e.target.value)}
        placeholder="Shkposti"
        type="email"
        sx={{ mb: 2 }}
      />

      <Typography variant="h6" sx={{ mt: 2 }}>
        Puhelinnumero
      </Typography>
      <TextField
        fullWidth
        value={phone}
        onChange={(e) => setPhone(e.target.value)}
        placeholder="Puhelinnumero"
        type="tel"
        sx={{ mb: 2 }}
      />

      <Typography variant="h6" sx={{ mt: 2 }}>
        Ik
      </Typography>
      <FormControl fullWidth sx={{ mb: 2 }}>
      <InputLabel id="age-label"></InputLabel>
      <Select
        labelId="age-label"
        value={age}
        onChange={(e) => setAge(e.target.value)}
        displayEmpty
      >
      <MenuItem value="" disabled>
        Valitse ik
      </MenuItem>
      {Array.from({ length: 83 }, (_, i) => 18 + i).map((ageValue) => (
      <MenuItem key={ageValue} value={ageValue}>
      {ageValue}
      </MenuItem>
      ))}
      </Select>
      </FormControl>

      <Typography variant="h6" sx={{ mt: 2 }}>
        Kokonaissumma: {totalPrice}
      </Typography>

      {error && (
      <Typography color="error" sx={{ mt: 2 }}>
      {error}
      </Typography>
      )}

      <Button
        variant="contained"
        color="primary"
        fullWidth
        sx={{ mt: 2 }}
        onClick={handleDialogOpen}
      >
        Varaa jsenyys
      </Button>

      <Dialog open={isDialogOpen} onClose={handleDialogClose}>
        <DialogTitle>Kiitos tilauksestasi! Tilauksen yhteenveto ja listiedot lhetetn shkpostiin.</DialogTitle>
        <DialogContent>
          <Typography>Shkposti: {email}</Typography>
          <Typography>Puhelinnumero: {phone}</Typography>
          <Typography>Ik: {age}</Typography>
          <Typography>Jsenyyspaketti: {selectedPlan.name}</Typography>
          <Typography>Lispalvelut: {Object.keys(selectedServices).filter(service => selectedServices[service]).join(', ') || 'Ei lispalveluja'}</Typography>
          <Typography>Paikkakunta: {selectedLocation || 'Ei valittu'}</Typography>
          <Typography>Kokonaissumma: {totalPrice}</Typography>
          <Typography>Listiedot: Maksu ja lispalvelujen ajanvalinta suoritetaan ensimmisell kynnillsi.</Typography>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleDialogClose} color="primary">
            Sulje
          </Button>
        </DialogActions>
      </Dialog>
    </Container>
  );
};

export default App;

